const pedido = {
    nombreCliente: '',
    telefono: '',
    email: '',
    mensaje: '',
    imagenes: []
}

let bgCard = document.querySelector('.bgDark');
let cardForm = document.querySelector('.form');

document.addEventListener('DOMContentLoaded', function(){

    mostrarFormulario();
    mostrarImagenes();
    obtenerDatos();

});

async function mostrarImagenes(){
    try{

        const resultado = await fetch('./imagenes.json');
        const db = await resultado.json();

        const {imagenes} = db;

        imagenes.forEach( imagen =>{
            const { id, nombre, ubicacion }= imagen;

            //DOM Script
            gallery(ubicacion, id, nombre);
        } )
    } catch(error){

        console.log('error al cargar imagenes');
    
    }
}

function gallery(url, id, nombreImg){
    const galeria = document.querySelector('.galeria-imagenes');

    const imagen = document.createElement('IMG');
    imagen.src = url;
    imagen.classList.add('elemento-imagen')
    //Darle un Id set en HTML 5 para la imagen
    imagen.dataset.imagenId = id;
    imagen.dataset.nombreImg = nombreImg;

     const lista = document.createElement('DIV');
     lista.classList.add('imagen');

    //selecciona una imagen con event handler
    lista.onclick = selectedImg;

     lista.appendChild(imagen);

     galeria.appendChild(lista);
}

function selectedImg(e){

    let img = e.target;
    let divImg = img.parentNode;
    
    if(divImg.classList.contains('selected')){
        divImg.classList.remove('selected');

        const id =parseInt(img.dataset.imagenId);
        removeImg(id);
    } else {
        divImg.classList.add('selected');
        
        const imagenObj ={
            id: parseInt(img.dataset.imagenId),
            nombreImg: img.dataset.nombreImg
        }
        addImg(imagenObj);
    }

    activeBtn();
}

function addImg(imagenObj){
    const {imagenes}=pedido;
    pedido.imagenes =[...imagenes, imagenObj];
    // console.log(pedido);
}

function removeImg(id){
    const {imagenes}=pedido;
    pedido.imagenes=imagenes.filter( imagen => imagen.id !== id );
    // console.log(pedido);
}

function mostrarFormulario(){
    let done = document.querySelector('#done');
    let close = document.querySelector('.close-form');

    done.addEventListener('click', showForm);

    close.addEventListener('click', hiddenForm);
}

function showForm(){
    const {imagenes} =pedido;

    if(imagenes.length >=1){
        cardForm.style.bottom = '2rem';
        bgCard.style.opacity = '0.8';
        bgCard.style.pointerEvents = 'unset';
    }
}

function hiddenForm(){
    cardForm.style.bottom = '-95rem';
    bgCard.style.opacity = '0.0';
    bgCard.style.pointerEvents = 'none';
    console.log(pedido);
}

function activeBtn(){
    let btn = document.querySelector('#done')
    const {imagenes} = pedido;

    if(imagenes.length >= 1){
        if(btn.classList.contains('inactive')){
            btn.classList.remove('inactive');
            btn.classList.add('active');
            btn.style.pointerEvents= 'unset';
        };
    } else{
        if(btn.classList.contains('active')){
            btn.classList.remove('active');
            btn.classList.add('inactive');
            btn.style.pointerEvents= 'none';
        };
    }
}

function obtenerDatos(){
    const nombreInput = document.querySelector('#nombre');
    const telefonoInput = document.querySelector('#telefono');
    const emailInput = document.querySelector('#email');
    const mensajeInput = document.querySelector('#mensaje');
    
    nombreInput.addEventListener('change', e =>{
        const inputNombre = e.target.value.trim();
        pedido.nombreCliente= inputNombre;
    })

    telefonoInput.addEventListener('change', e =>{
        const inputTel = e.target.value.trim();
        pedido.telefono= inputTel;
    })

    emailInput.addEventListener('change', e =>{
        const inputEmail = e.target.value.trim();
        pedido.email= inputEmail;
    })

    mensajeInput.addEventListener('change', e =>{
        const inputMensaje = e.target.value.trim();
        pedido.mensaje = inputMensaje;
    })

}